import React, { useState } from "react";
import styles from "./Contact.module.css";
import Between from "../../UI/Between/Between";
import MainTitle from "../../UI/MainTitle/MainTitle";
import Input from "../../UI/Input/Input";
import Spinner from "../../UI/Spinner/Spinner";
import emailjs from "emailjs-com";
import EmailValidator from "email-validator";

const Contact = (props) => {
  const [loading, setLoading] = useState(false);
  const [send, setSend] = useState(false);
  const [formValid, setFormValid] = useState(false);
  const [contactForm, setContactForm] = useState({
    name: {
      elementType: "input",
      elementConfig: {
        type: "text",
        placeholder: "Enter your name",
      },
      value: "",
      validation: {
        required: true,
        string: true,
      },
      valid: false,
      touched: false,
      errorMsg: "Please enter a valid name",
    },
    email: {
      elementType: "input",
      elementConfig: {
        type: "email",
        placeholder: "Enter email address",
      },
      value: "",
      validation: {
        required: true,
        email: true,
      },
      valid: false,
      touched: false,
      errorMsg: "Please enter a valid email",
    },
    subject: {
      elementType: "input",
      elementConfig: {
        type: "text",
        placeholder: "Enter subject",
      },
      value: "",
      validation: {
        required: true,
      },
      valid: false,
      touched: false,
      errorMsg: "Please enter a valid subject",
    },
    message: {
      elementType: "textarea",
      elementConfig: {
        type: "text",
        placeholder: "Enter Message",
      },
      value: "",
    },
  });

  // const orderHandler = (e)=>{
  //     e.preventDefault();
  //     setLoading(true);
  //     const formData={}
  //     for(let formDataElement in contactForm){
  //         formData[formDataElement]=contactForm[formDataElement].value
  //     }
  //     axios.post("https://ala-hamadi.firebaseio.com/contact.json",formData)
  //         .then(()=>{
  //             setLoading(false);
  //         })
  //         .catch(()=>{
  //             setLoading(false)
  //         })
  // }

  const sendEmail = (e) => {
    setLoading(true);
    emailjs
      .sendForm(
        "service_gw8hx68",
        "template_jblz21g",
        e.target,
        "user_FK2Ivrr5quvjY6eRKVGBa"
      )
      .then(
        (response) => {
          setLoading(false);
          setSend(true);
        },
        (err) => {
          setLoading(false);
        }
      );
    setContactForm({
      name: {
        elementType: "input",
        elementConfig: {
          type: "text",
          placeholder: "Enter your name",
        },
        value: "",
        validation: {
          required: true,
          string: true,
        },
        valid: false,
        touched: false,
        errorMsg: "Please enter a valid name",
      },
      email: {
        elementType: "input",
        elementConfig: {
          type: "email",
          placeholder: "Enter email address",
        },
        value: "",
        validation: {
          required: true,
          email: true,
        },
        valid: false,
        touched: false,
        errorMsg: "Please enter a valid email",
      },
      subject: {
        elementType: "input",
        elementConfig: {
          type: "text",
          placeholder: "Enter subject",
        },
        value: "",
        validation: {
          required: true,
        },
        valid: false,
        touched: false,
        errorMsg: "Please enter a valid subject",
      },
      message: {
        elementType: "textarea",
        elementConfig: {
          type: "text",
          placeholder: "Enter Message",
        },
        value: "",
      },
    });
  };

  const inputChangeHandler = (event, id) => {
    const updateOrderForm = {
      ...contactForm,
    };
    const updateElementForm = {
      ...updateOrderForm[id],
    };
    updateElementForm.value = event.target.value;
    if (updateElementForm.validation)
      updateElementForm.valid = checkValidity(
        updateElementForm.value,
        updateElementForm.validation
      );
    updateElementForm.touched = true;
    updateOrderForm[id] = updateElementForm;

    let formValid = true;
    for (let key in updateOrderForm) {
      if (updateOrderForm[key].valid != null)
        formValid = updateOrderForm[key].valid && formValid;
    }
    setContactForm(updateOrderForm);
    setFormValid(formValid);
  };

  const checkValidity = (value, rules) => {
    let isValid = true;
    if (rules.required) isValid = value.trim() !== "" && isValid;
    if (rules.minLen) isValid = value.length >= rules.minLen && isValid;
    if (rules.email) {
      isValid = EmailValidator.validate(value) && isValid;
    }
    if (rules.string) {
      const pattern = /^[a-zA-Z]+(([',. -][a-zA-Z ])?[a-zA-Z]*)*$/g;
      isValid = pattern.test(value) && isValid;
      //const pattern = /[a-z0-9!#$%&'*+/=?^_`{|}~-]+(?:\.[a-z0-9!#$%&'*+/=?^_`{|}~-]+)*@(?:[a-z0-9](?:[a-z0-9-]*[a-z0-9])?\.)+[a-z0-9](?:[a-z0-9-]*[a-z0-9])?/;
      //isValid = pattern.test(contact.value) && isValid
    }
    return isValid;
  };
  const elementsFormArray = [];
  for (let key in contactForm) {
    elementsFormArray.push({
      id: key,
      config: contactForm[key],
    });
  }
  let form = (
    <form
      className={`${styles.contactForm} row`}
      id='myForm'
      onSubmit={sendEmail}
    >
      <div className='col-md-6'>
        {elementsFormArray
          .filter((element) => element.config.elementType !== "textarea")
          .map((element) => {
            return (
              <div key={element.id} className={`${styles.formGroup} `}>
                <Input
                  key={element.id}
                  elementType={element.config.elementType}
                  elementConfig={element.config.elementConfig}
                  value={element.config.value}
                  changed={(event) => inputChangeHandler(event, element.id)}
                  invalid={!element.config.valid}
                  shouldValidate={element.config.validation}
                  touched={element.config.touched}
                  errorMsg={element.config.errorMsg}
                  name={element.id}
                />
              </div>
            );
          })}
      </div>
      <div className='col-md-6'>
        {elementsFormArray
          .filter((element) => element.config.elementType === "textarea")
          .map((element) => {
            return (
              <div key={element.id} className={`${styles.formGroup} `}>
                <Input
                  key={element.id}
                  elementType={element.config.elementType}
                  elementConfig={element.config.elementConfig}
                  value={element.config.value}
                  changed={(event) => inputChangeHandler(event, element.id)}
                  invalid={!element.config.valid}
                  shouldValidate={element.config.validation}
                  touched={element.config.touched}
                  errorMsg={element.config.errorMsg}
                  name={element.id}
                />
              </div>
            );
          })}
      </div>
      <div className='col-md-12 text-right'>
        <button
          disabled={!formValid}
          type='submit'
          value='submit'
          className={styles.submitBtn}
        >
          Send Message
        </button>
      </div>
    </form>
  );
  if (loading) form = <Spinner />;

  return (
    <React.Fragment>
      <Between />
      <section className={styles.contactArea} id='Contact'>
        <MainTitle>Contact me </MainTitle>
        <div className='container'>
          <div className='col-lg-12'>
            <div className={styles.succesDiv}>
              {send ? (
                <span className={styles.succes}>
                  <i className='fa fa-check'></i>Your Message was Sent
                  Successfully.
                </span>
              ) : null}
            </div>
            {form}
          </div>
        </div>
      </section>
    </React.Fragment>
  );
};
export default Contact;
