import React from "react";
import "./MyCertifications.css";
import Slider from "react-slick";
import certif1 from "../../assets/images/certificate1.PNG";
import certif2 from "../../assets/images/certificate2.PNG";
import certif3 from "../../assets/images/certificate3.PNG";
import certif4 from "../../assets/images/certificate4.PNG";
import certif5 from "../../assets/images/certificate5.PNG";
import certif6 from "../../assets/images/certificate6.PNG";
import certif7 from "../../assets/images/certificate7.PNG";
import certif8 from "../../assets/images/certificate8.PNG";
import certif9 from "../../assets/images/certificate9.PNG";
import icon from "../../assets/images/icon.png";
const MyCertifications = (props) => {
  const Certifications = [
    {
      title: "Computer Science Career Path Certification",
      image: certif1,
      link:
        "https://drive.google.com/file/d/1Xzb36Dfrqmkm68bEF8RvNupwzwxSgkgp/view?usp=sharing",
    },

    {
      title: "Git Certification",
      image: certif2,
      link:
        "https://drive.google.com/file/d/1Aj_ysfbbTMdxloYgZCeVQKjvn8RIsBJ3/view?usp=sharing",
    },

    {
      title: "Command Line Certification",
      image: certif3,
      link:
        "https://drive.google.com/file/d/1Dz-2qfdGdbKajs2dr-29lvSzaGebZ-uc/view?usp=sharing",
    },

    {
      title:
        "React - The Complete Guide (incl Hooks, React Router, Redux) Certification",
      image: certif4,
      link:
        "https://drive.google.com/file/d/1cRFVFeFLVTXBA60Fc7_0O9z3CAld9zE8/view?usp=sharing",
    },

    {
      title: "Server-side Development with NodeJS, Express and MongoDB",
      image: certif9,
      link:
        "https://drive.google.com/file/d/1VSG6RHlSJuajKFpFBDEChFIl8iGpdOuX/view?usp=sharing",
    },

    {
      title: "ISI Collegiate Programming Contest Certification",
      image: certif5,
      link:
        "https://drive.google.com/file/d/1wfCSiTJ6ZwPZyPOXM_LyI20-0aBq8stC/view?usp=sharing",
    },

    {
      title: "Tunisian Collegiate Programming Contest Certification",
      image: certif6,
      link:
        "https://drive.google.com/file/d/186DaIJHk_SzuvR1JLE0Tx_uVHIyoQYts/view?usp=sharing",
    },

    {
      title: "Acknowledgment Certification",
      image: certif7,
      link:
        "https://drive.google.com/file/d/123SWGZ8L2fe1jw1N81B027Rd5q28-GHJ/view?usp=sharing",
    },

    {
      title: "Tunisian Olympiad in Programming Certification",
      image: certif8,
      link:
        "https://drive.google.com/file/d/10Karw60a8vAZmatdCk28A50SCyIRjJTw/view?usp=sharing",
    },
  ];

  const settings = {
    className: "center",
    centerMode: true,
    centerPadding: "0px",
    dots: true,
    infinite: true,
    slidesToShow: 3,
    slidesToScroll: 1,
    autoplay: true,
    speed: 1000,
    autoplaySpeed: 3500,
    cssEase: "linear",
    pauseOnHover: false,
    responsive: [
      {
        breakpoint: 770,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1,
        },
      },
    ],
  };

  return (
    <Slider {...settings}>
      {Certifications.map((Certification) => {
        return (
          <div key={Certification.title} className='certif'>
            <div className='h_gallery_item'>
              <div className='g_item_text'>
                <h4>{Certification.title}</h4>
              </div>
              <div className='g_img_item'>
                <img className='img-fluid' src={Certification.image} alt='' />
                <a
                  className='light'
                  href={Certification.link}
                  target='_blank'
                  rel='noopener noreferrer'
                >
                  <img src={icon} alt={Certification.title} />
                </a>
              </div>
            </div>
          </div>
        );
      })}
    </Slider>
  );
};

export default MyCertifications;
